.about__scroll__container {
    position: relative;
    height: 1000vh;
    width: 100%;
    background-color: rgb(12, 12, 12);
  }
  
  .about__div__section {
    position: sticky;
    height: 95dvh;
    background-color: rgba(255, 255, 255, 0.014);
    border: rgba(255, 255, 255, 0.137) 1px solid;
    top: 2.5dvh;
    max-width: 95%;
    /* aspect-ratio: 9/16; */
    margin: 0 auto;
    border-radius: 0.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.about__div__section::before {
  position: absolute;
  inset: 0;
  background-color: red;
}

.about__sub__me {
  font-size: 1rem;
  color: whitesmoke;
  position: absolute;
  bottom: 1em;
  right: 1em;
}
.general__span-up {
  background-color: rgb(240, 85, 85);
  padding: .25em 5em;
}
.about__light {
  position: absolute;
  pointer-events: none;
  background: rgb(110,231,183);
  background: linear-gradient(90deg, rgba(110,231,183,1) 0%, rgba(167,139,250,1) 100%);
  border-radius: 50%;
  filter: blur(200px);
  width: 300px;
  height: 300px;
  z-index: -1;
  animation: spin 10s infinite linear alternate;
}
  .__section__blur {
    height: 100vh;
    width: 100%;
    position: absolute;
    z-index: 0;
    backdrop-filter: blur(200px);
  }

.about__sub__me-v2 {
  font-size: 1rem;
  color: whitesmoke;
  position: absolute;
  top: 1em;
  left: 1em;
}
  
.about__sub__me-v3 {
  font-size: 1rem;
  color: whitesmoke;
  position: absolute;
  bottom: 1em;
  left: 1em;
}
.about__sub__me-v4 {
  font-size: 1rem;
  color: whitesmoke;
  position: absolute;
  bottom: .5em;
  left: 1em;
}

.about__scroll__progress__bar {
  width: 0;
  height: 2px;
  background-color: rgb(255, 255, 255);
}
  
.about__scroll__progress__bar::before {
  content: "";
  position: absolute;
  width: 100px;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.479);
}
  
.about__scroll {
  color: rgba(255, 255, 255, 0.822);
  font-size: 8rem;
  font-family: "HEAD";
  letter-spacing: 6px;
}
.about__section1 {
  position: absolute;
  width: 50%;
}
.about__section1__p{
  color: whitesmoke;
  font-size: 2rem;
  text-align: left;
}
.about__section2 {
  display: grid;
  position: absolute;
  grid-template-columns: repeat(3, 250px);
  grid-template-rows: repeat(2, 250px);
  justify-items: center;
  background-color: rgba(255, 255, 255, 0);
  color: white;
  padding: 1em;
  gap: 1em;
  height: fit-content;
}
.about__section2__child {
  background-color: rgba(255, 255, 255, 0.041);
  aspect-ratio: 1;
  border-radius: .5em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
  flex-direction: column;
  transition: .25s ease;
  border: rgba(255, 255, 255, 0) 1px solid;
  grid-auto-flow: column dense;
  backdrop-filter: blur(4px);


}

.about__section2__child__more {
  background-color: rgba(255, 255, 255, 0.041);
  width: 100%;
  height: 250px;
  aspect-ratio: 1;
  border-radius: .5em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 3em;
  transition: .25s ease;
  border: rgba(255, 255, 255, 0) 1px solid;
  grid-column: 1 / 4;
  backdrop-filter: blur(4px);
}
.about__section2__child__more__conto{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3em;
  transition: .25s ease;
  border: rgba(255, 255, 255, 0) 1px solid;
  grid-column: 1 / 4;
}
.about__section2__child__more__conto2{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.5em;
  transition: .25s ease;
  border: rgba(255, 255, 255, 0) 1px solid;
  grid-column: 1 / 4;
}
.about__section2__child__more__cont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;

}
.about__section2__child:hover {
  background-color: rgba(255, 255, 255, 0.082);
  border: rgba(255, 255, 255, 0.452) 1px solid;

}
.about__section2__child__more:hover {
  background-color: rgba(255, 255, 255, 0.082);
  border: rgba(255, 255, 255, 0.452) 1px solid;
}


.about__para {
  font-size: 1.5rem;
}
.about__logo {
  transition: .15s;
}
.about__logo-little {
  background-color: rgba(255, 255, 255, 0.027);
  border: rgba(255, 255, 255, 0.233) 1px solid;
  padding: 7px;
  border-radius: 7px;
}

.contact__container {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 2em;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 1em
}
.contact__container__title {
  font-family: DIN;
  color: white;
  font-weight: 900;
  font-size: 6rem;
}
.contact__container__mail {
  font-family: DIN;
  color: white;
  font-weight: 200;
  font-size: 3.5rem;
}

@keyframes spin {
  from {
    transform: scaleX(1.50) rotate(0);
  }

  to {
    transform: scaleX(1.20) rotate(360);

  }
}


@media (max-width: 1200px) {
  .about__div__section {
    position: sticky;
    height: 95dvh;
    background-color: rgba(255, 255, 255, 0.014);
    border: rgba(255, 255, 255, 0.137) 1px solid;
    top: 2.5dvh;
    max-width: 95%;
    /* aspect-ratio: 9/16; */
    margin: 0 auto;
    border-radius: 0.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.about__scroll {
  font-size: 3em
}
.about__section1 {
  width: 95%;
}
.about__section1__p {
  font-size: 1.5rem;
}

.about__section2 {
  
}
}

@media (max-width: 800px) {
  .contact__container__title {
    font-size: 4rem;
  }
  .contact_littles > p{
    font-size: 2rem;
  }
}
@media (max-width: 520px) {
  .contact__container__title {
    font-size: 3rem;
  }
  .contact_littles > p{
    font-size: 1.5rem;
  }
}
@media (max-width: 412px) {
  .contact__container__title {
    font-size: 2rem;
  }
  .contact_littles > p{
    font-size: 1rem;
  }
}
@media (max-width: 900px) {
  .about__section2 {
    grid-template-columns: repeat(3, 200px);
  grid-template-rows: repeat(2, 200px);
  
}

body {
  cursor: auto;
}

.projets__container__child {
  position: relative;
  aspect-ratio: 9/16!important;
}
.projets__container {
  gap: 1em!important;
}
  .about__section2__child__more__conto {
    gap: 1em;
  }
}
@media (max-width: 700px) {
  .about__section2 {
    grid-template-columns: repeat(3, 150px);
  grid-template-rows: repeat(2, 150px);
  
  
  }
  .about__section2__child__more__conto {
    gap: 1em;
  }
  .about__para {
    font-size: 1.1rem;
  }
}

@media (max-width: 530px) {
  .about__section2 {
    grid-template-columns: repeat(3, 100px);
  grid-template-rows: repeat(2, 100px);
  
  
  }
  .about__section2__child__more__conto {
    gap: 1em;
  }
  .about__para {
    font-size: 1.1rem;
  }
  .about__section2__child__more__conto2 {
    flex-wrap: wrap;
    width: none;
    gap: .5em;
  }
}
@media (max-width: 370px) {
  .about__section2 {
    grid-template-columns: repeat(3, 80px);
  grid-template-rows: repeat(2, 80px);
  }
}

