.out__circle {
    width: 100px;
    border: white 1px solid;
    height: 100px;
    position: fixed;
    z-index: 999;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    mix-blend-mode: difference;
    pointer-events: none;
}
.inside__circle {
    width: 30px;
    height: 30px;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    position: fixed;
    z-index: 999;
    mix-blend-mode: difference;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;

}
.contact_littles {
    display: flex;
    gap: 1em;
    justify-content: center; 
    align-items: center;
}

.inside__circle__label {
    text-align: center;
    font-size: 1.725rem;
    font-family: "DIN";
    font-weight: 200;
    pointer-events: none;
    user-select: none;

}