.projet__container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    position: absolute;
    backdrop-filter: blur(4px) brightness(.4);
    color: whitesmoke;
    flex-direction: column;
    padding: 2em;
}

.projet__container__tech {
    display: grid;
    gap: .5em;
    grid-template-columns: repeat(3, 1fr);
}

.projet__container__title {
    font-size: 3rem;
    font-family: "DIN";
    font-weight: 900;
}
.projet__container__label {
    font-size: 2rem;
    font-family: DIN;
    font-weight: 200;
    font-style: italic;
}
.projet__container__desc {
    font-size: 1.5rem;
     font-family: DIN-P;
    font-weight: 500;
    margin-top: .5em;
    margin-bottom: 1em;
}

@media (max-width: 900px) {
    .projet__container__title {
        font-size: 1.5rem;
        font-family: "DIN";
        font-weight: 900;
    }
    .projet__container__label {
        font-size: 1rem;
    }
    .projet__container__desc {
        font-size: 1rem;
        text-align: center;
    }
    .projet__container {
        padding:1em ;
    }
}