.projets__container {
    display: flex;
    position: absolute;
    z-index: 99999;
    width: fit-content;
    height: 80%;
    left: 0;
    gap: 4em;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    pointer-events:visible;
}

.projets__container__child {
    background-position: center;
    background-size: cover;
    height: 80%;
    aspect-ratio: 16/9;
    transition: background-image .15s;

    /* background-image: url("../../assets/projets/carrousel-brad.jpg"); */
}
.projets__container__child:nth-child(1):hover {
    background-image: url("../../assets/projets/skin.gif");
}
.projets__container__child:nth-child(2):hover {
    background-image: url("../../assets/projets/overwatch.gif");
}
