
@font-face {
  font-family: "DIN";
  src: url("../fonts/DINPro-Black.woff2") format("woff2");
  font-weight: 900;
}  

@font-face {
  font-family: "DIN";
  src: url("../fonts/DINPro-Bold.woff2") format("woff2");
  font-weight: 700;
}  
@font-face {
  font-family: "HEAD";
  src: url("../fonts/PPCasa-Regular.woff2") format("woff2");
  font-weight: 500;
}  
@font-face {
  font-family: "DIN-P";
  src: url("../fonts/DINPro.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}  

@font-face {
  font-family: "DIN";
  src: url("../fonts/DINPro-Light.woff2");
  font-weight: 200;
  /* font-style: normal; */
}
  
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

