body::-webkit-scrollbar {
  /* display: none; */
}


body {
  /* -ms-overflow-style: none;   */
  /* scrollbar-width: none;   */
  cursor: none;
  font-family: "DIN";
}

.App {
  width: 100%;
  height: 100%;
}
.scroll200vh {
  position: fixed;
  z-index: -999;
  height: 200vh;
}
.container {
    display: flex;
    justify-content: space-between;
    align-items:start;
    background-color: #F5F5F5;
    color: #282828;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    flex-direction: column;
    perspective: 1000;
  }
  .desc__para {
    font-family: "DIN";
    font-weight: 200;
    width: 500px;
    line-height: 2.5rem;
    text-align: justify;
    font-size: 2rem;

  }
  .right {
    align-self: end;
  }
  .sub {
    font-size: 16.30vw;
    font-family: "HEAD";
    line-height: 75%;
    font-weight: normal;
    margin: 0;
    font-style: normal;

  }
  .developer {
    font-size: 16vw;
    line-height: 75%;
    font-family: "DIN";
    font-weight: 900;
    margin: 0;
    margin-left: 2rem;
    margin-right: 2rem;
  }
  .cont {
    display: flex;
    align-items: center;
  }

  .desc {
    font-family: "DIN-P";
    font-weight: 500;
    width: 350px;
    text-transform: uppercase;
    text-align: justify;
    font-size: 1.5rem;
    position: absolute;
    bottom: 1rem;
    left: 2rem;
    overflow-x: hidden;
    z-index: 100;
  }
  .desc > p {
    position: relative;
    font-family: "DIN-P";
    font-weight: 500;
    width: 350px;
    text-transform: uppercase;
  }

  @keyframes clin {
    from {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .code_bar_bar {
    opacity: 0;
    animation: clin .35s alternate-reverse forwards;
    mix-blend-mode: difference;
  }


  @media (max-width: 800px) {
    .container {
        justify-content: flex-start;
    }
    .desc {
      font-family: "DIN-P";
      font-weight: 500;
      width: 250px;
      text-transform: uppercase;
      text-align: justify;
      font-size: 1.5rem;
      position: absolute;
      bottom: 1rem;
      left: 50%;
      overflow-x: hidden;
      z-index: 100;
      transform: translateX(-50%);
  }
  .desc__para {
    width: 100%!important;
    font-size: 1.5rem!important;
  }
  .code_bar {
    gap: 3px;
  }
}

